.circle {
    box-shadow: 0 15px 25px 0 rgba(0, 0, 0, .23);
    width: 160px;
    height: 160px;
    border-radius: 50%;
    margin: 0 auto;
    opacity: 70%;
}

.circleContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    cursor: grab;
}
